.highlight{
    color: var(--primary);
}
ul{
    margin: 0;
    padding: 0;
}
li{
    list-style: none;
}
.footer_section{
    width: 100%;
    background-color: var(--smooth-purple);
    margin-top: 10vh;
    font-family: 'Roboto', sans-serif;
    color: white;

}
.footer_container{
    width: 90%;
    margin: auto;
    padding: 5vh 30px;
}
.footer_header{
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
}
.footer_branding{
    color: white;
    font-size: 25px;
}
.footer_about{
    background-color: #5F7D95;
    color: white;
    padding: 15px 30px;
    border-radius: 8px;
}
.horizontal_divider{
    height: 1px;
    background-color: hsla(0, 0%, 100%, 0.5);

}
.footer_body{
   display: grid;
   grid-template-columns: repeat(4, 1fr);
   margin-top: 1.5rem;
   margin-bottom: 1.5rem;
}
.footer_navigation,.footer_otherlinks,.footer_contact{
    line-height: 2.5rem;
}
.footer_navigation_title h3{
    font-size: 25px;
    color: var(--primary);
   
}
.footer_ul{
    display: flex;
    margin-top: 20px;
    gap: 1rem;
}
.footer_ul li:hover{
    color: var(--primary);
}
@media(max-width:800px){
    .footer_body{
        grid-template-columns: repeat(2, 1fr);
    }
}
@media(max-width:430px){
    .footer_header{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
    }
    .footer_about{
        max-width: fit-content;
    }
    .footer_body{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
    }
    .footer_navigation_title h3{
        font-size: 25px;
        color: var(--primary);
        text-align: center;
    }
    .footer_navigation,.footer_contact{
        text-align: center;
    }
    .footer_navigation,.footer_otherlinks,.footer_contact{
        line-height: 2.0rem;
    }
}