.blog_container{
    width: 90%;
    margin: auto;
}
.blog_posts .blog_container{
    display: grid;
    grid-template-columns: repeat(3 ,1fr);
    grid-gap: 1rem 4rem;
}
.blog_cards{
    background-color: white;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 1.5rem;
}
.blog_header{
    text-align: center;
    margin-bottom: 10vh;
}
.blog_header .header_divider{
    width: 8% !important;
}
.blog_content{
    width: 100%;
}
.blog_cards{
    box-shadow: 0 5px 25px rgb(0 0 0 / 10%);
}
.blog_cards .blog_image {
    width: 100%;
    height: 200px;
}
.blog_cards .blog_image .blog_img{
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 10px;
}
.blog_title{
    margin-top: 1.5rem;
    text-align: center;
}
.blog_info{
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
}
.blog_info a{
    background-color: var(--smooth-purple);
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
}
.blog_info a:hover{
    background-color: var(--light-purple);
}

@media(max-width:1200px){
    .blog_posts .blog_container{
        display: grid;
        grid-template-columns: repeat(2 ,1fr);
        grid-gap: 4rem;
    }
}
@media(max-width:678px){
    .blog_posts .blog_container{
        display: grid;
        grid-template-columns: repeat(2 ,1fr);
        grid-gap: 1rem;
    }
   
}
@media(max-width:600px){
    .blog_posts .blog_container{
        display: grid;
        grid-template-columns: repeat(1 ,1fr);
        grid-gap: 1rem;
    }

    /* .blog_info a{
        background-color: white;
        color: var(--light-purple);
        padding: 0;
        border-radius: 5px;
    } */
}
@media(max-width:300px){
    .blog_info a{
        padding: 0;
        background-color: inherit;
        font-size: 14px;
        color: var(--smooth-purple);
    }
}