.slide1{
    background-image: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url('../../assets/images/owolabispecials/295858718_10223184318820486_9214812318906634613_n.jpg');
}
.slide2{
    background-image: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url('../../assets/images/owolabispecials/79407461_10216594473678476_3571104451508830208_n.jpg');
}
.slide3{
    background-image: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url('../../assets/images/owolabispecials/owolabibg.jpg');
    background-position: top !important;
}

.showcase_section{
    height: 100vh;
 
}
.slide{
    clip-path:polygon(50% 0%, 100% 0, 100% 95%, 50% 100%, 0 95%, 0 0, 24% 0);
    height: 100vh;
    background-position: center;
    background-size: cover;
}
.swiper-button-prev{
    
    color: var(--primary);
  
    
}
.swiper-button-next{
    
    color:var(--primary);
 
}


.swiper-pagination-clickable .swiper-pagination-bullet-active{
    background: var(--primary);
    width: 10px;
    height: 10px;
}
.showcase_content{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: inherit;
    color: white;
    width: 80%;
    margin: auto;
    text-align: center;
    
}

.content_paragraph{
    letter-spacing: 5px;
    line-height: 40px;
}

@media(max-width:1024px){
    .showcase_section{
        height: 88vh;
    }
    .slide{
        height: 88vh;
    }
}
@media(max-width:680px){
    .swiper-button-next,
    .swiper-button-prev{
        background-color: unset;
        height: 0px !important;
        color: var(--primary);
        
    }
    .showcase_section{
        height: 80vh;
     
    }
    .showcase_content{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        height: inherit;
        color: white;
        width: 80%;
        margin: auto;
        text-align: center;
       
    }
    .slide{
        height: 80vh;
    }
}

@media(max-width:500px){
  
    .swiper-button-next,
    .swiper-button-prev{
        display: none;
    }
    .content_paragraph{
        letter-spacing: 5px;
        line-height: 20px;
    }
    .showcase_header_text{
        line-height: 40px;
        padding-top: 10px;
    }
    .showcase_paragraph{
        line-height: 20px;
    }
    .showcase_section{
        height: 70vh;
    }
    .slide{
        height: 70vh;
    }
}
