.about_section{
    width: 100%;
    font-family: 'Roboto', sans-serif;
    margin-top: 20vh;
}
.about_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 80%; 
    margin: auto;
    gap: 3rem;
    line-height: 25px;
    
}
.about_header{
    width: 90%;
    line-height: 40px;
    font-size: 19px;
    color: var(--header-black);
    position: relative;
}

.divider{
    width: 88px;
    height: 3px;
    margin-top: 8px;
    margin-bottom: 21px;
    border-radius: 1.5px;
    background-color: var(--primary);

}
.about_text{
    text-align: justify;
    color: var(--text-gray);
    font-size: 19px;
    line-height: 25px;
}
.about_link{
    margin-top: 5vh;
}
.about_link a{
    background-color: var(--primary);
    color: white;
    padding: 15px  20px;
    border-radius: 5px;
    font-size: 18px;
    transition: 0.30s background ease;
}
.about_link a:hover{
    background-color:var(--primary-hover);
}
 /* Responsiveness  */
@media(max-width:768px){
    .about_content{
        display: grid;
        grid-template-columns: 1fr;
    }
    .about_content{
        gap:2rem;
        
    }
    
} 
@media(max-width:500px){
    .about_content{
        gap:1.5rem;
        
    }
}