.contact_showcase{
    text-align: center;
    
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(rgba(69, 24, 75, 0.5), rgba(85, 27, 139, 0.5)),url('../../assets/images/phone_bg.jpg');
    background-position: left;
    background-size: cover;
    background-blend-mode:darken;
    color: white;
    

    position: relative;
}

.contact_header .divider{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    margin: 2vh auto;
}
.contact_showcase h3{
    font-size: 40px;
}
.contact_title{
    text-align: center;
    margin-top: 2rem;
    font-size: 22px;
    clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 75% 75%, 75% 100%, 50% 75%, 0% 75%);
    background-color: var(--primary);
    height: 30vh;
    background-color: var(--smooth-purple);
    color: var(--primary);
    width: 80%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center
    
}
.contact_wrapper{
    
    display: grid;
    width: 90%;
    margin:10vh auto;
    grid-template-columns: 45% 50%;
    gap: 2rem;
}
.social_card{
    display: flex;
    margin: 20px 0;
    /* margin-bottom: 1rem; */
   
}
.social_brand{
    background-color: var(--smooth-purple);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    padding: 20px;
}
.social_link{
    line-height: 17px;
    background-color: white;
   padding:10px 15px;
   line-height: 20px;
}
.social_link h4{
    color: var(--smooth-purple);
    font-size: 20px;
}
.social_link h4:hover{
    text-decoration: underline;
}
.contact_form form{
    display: flex;
    flex-direction: column;
    margin: auto;
}
.contact_form form input{
   margin-bottom: 1.5rem;
   padding: 1rem;
}
.contact_form form textarea{
    height: 20vh;
    padding: 1rem;
    resize: none;
}
.contact_form form button{
    max-width:fit-content;
   background-color: var(--primary);
   color: white;
   border: none;
   outline: none;
   font-size: 16px;
   padding: 1rem 2rem;
   margin-top: 1rem;
   cursor: pointer;
}
.contact_form form button:hover{
    background-color: var(-primary-hover);
}
@media(max-width:740px){
    .contact_wrapper{
        display: block;
        margin-top: 0;
    }
    .contact_title{
        text-align: center;
        margin-top: 2rem;
        margin-bottom: 2rem;
        font-size: 22px;
        
    }
    .contact_form form{
        margin-top: 3rem;
    }
}