.vision_section{
    height: 70vh;
    background-color: var(--smooth-purple);
   background-image: url('../../assets/images/blizzard.png');
   color: white;
   margin: 15vh auto;
}
.vision_content{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   width: 60%;
   margin: auto;
   height: inherit;
   text-align: center; 
   line-height: 2rem;
}
.vision_content h3{
    font-size: 40px;
}
.vision_text{
    font-size: 16px;
}
@media(max-width:900px){
    .vision_section{
        height: 50vh;
    }
}
@media(max-width:600px){
    .vision_content{
        width: 90%;
        margin: auto;
    }
}