.team_section{
   width: 90%;
   margin: auto;
   cursor: default;
}
.team_header{
    text-align: center;
    line-height: 25px;
    margin-top: 15vh;
    margin-bottom: 10px;
}
.team_header .divider{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10%;
    margin:10px auto 60px auto;
    
}
.swiper-button-next:after, .swiper-button-prev:after {
    font-family: swiper-icons;
    font-size: 20px;
    text-transform: none!important;
    letter-spacing: 0;
    background-color: white;
    padding: 20px;
    font-variant: initial;
    line-height: 1;
    height: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.team_wrapper .team_card{
    margin: 30vh auto;
}
.team_image{
    height: 50vh;
    width: 100%;
    overflow: hidden;
}
.team_image img{
    height: 100%;
    object-fit: cover;
    object-position:  center;
    width: 100%;
    transition: 0.30s ease;
  
}
.team_image img:hover{
    transform:scale(1.3);
}
.team_title{
    text-align: center;
    background-color: white;
    line-height: 20px;
    padding: 10px;
}

