 
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}


header{
    background-color: var(--smooth-purple);
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 12vh;
    padding: 0 3rem;
    position: fixed;
    z-index: 100;
    width: 100%;
   
}
.nav_brand{
    color: var(--primary);
    font-size: 20px;
}
.nav_brand span{
    color: var(--primary);
}
.content_link{
    background-color: var(--primary);
    padding: 15px 40px  !important;
    max-width: fit-content;
    border-radius: 7px;
}
.content_link:hover{
    background-color: var(--primary-hover);
    color: white !important;
}

.main_nav .nav_link{
    display: flex;
    color: white;
}
.main_nav .nav_link a{
    padding: 0 20px;
    cursor: pointer;   
}
.main_nav .nav_link a:hover{

    border-radius: 5px;
    color: var(--primary);

}
.mobile_nav{
    display: none;

}

/* Header Responsiveness */
@media(max-width:756px){
   
    header{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        justify-content: center;
        gap: 2rem;
      
    }
    .mobile{
        background-color: white;
        border-top: 2px solid var(--primary);
    }
   
    .main_nav{
        display: none;
    }
    .mobile_nav{
        display:flex;
        width: 100%;
        justify-content: center;
        flex-direction: column;
        transition: 1s ease;
    }
    .mobile_nav .nav_link{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        line-height: 4rem;
    

    }
    .hamburger{
        position: absolute;
        color: var(--primary);
        right: 5%;
        top: 4%;
        z-index: 100;

        cursor: pointer;
    }
}
@media(max-width:564px){
    header{
   
        padding: 1rem 3rem;
     
       
    }
}