.mission{
    margin-top: 10vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    
}
.mission_header{
    text-align: center;
    margin: auto;
    width: 40%;
}
.header_divider{
    height: 4px;
    background-color: var(--primary);
    width: 20%;
    border-radius: 5px;
    margin: 0.5rem auto;
    
}
.mission_header h2{
    font-size: 30px;
}
.mission_header p{
    text-align: center;
    font-size: 15px;
    line-height: 20px;
}
.mission_body{
    display: grid;
    width: 80%; margin: auto;
    grid-template-columns: repeat(3 , 1fr);
    /* gap: 2rem; */
    margin-top: 10vh;
    grid-gap: 2rem 1.5rem;
   
}

.mission_card{
    background-color: white;
    box-shadow: 0 5px 25px rgb(0 0 0 / 20%);
    padding:  25px;
    gap: 1rem;
    border-radius: 10px;
}

.card_ol{

    color: var(--primary);
    font-size: 22px;
    padding: 20px;
    display: flex;
    align-items: center;
    
}
.card_content h4{
    color: var(--smooth-purple);
}
@media(max-width:950px){
    .mission_body{
        grid-template-columns: repeat(2 ,1fr);
    }
}
@media(max-width:700px){
    .mission_header{
        text-align: center;
        margin: auto;
        width: 80%;
    }
}
@media(max-width:500px){
    .mission_body{
        grid-template-columns: repeat(1 ,1fr);
    }
    .mission_card{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .card_ol{
        height: 10vh;
        padding: 15px;
        max-width: fit-content;  
        border-radius: 15px;  
    }
   .card_content{
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
   }
}