.about_showcase{
    display: flex;
    height: 50vh;
    background-color:var(--dark-gray-bg);
    color: var(--smooth-purple);
}   
.about_showcase_content{
    display: flex;
    flex-direction: column;
    height: inherit;
    justify-content: center;
    align-items: center;
    line-height: 28px;

}
.about_vision{
    width: 70%;
    margin: auto;
    background-color: white;
    margin-top: 20vh;
    margin-bottom: 10vh;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 5px 10px #c4c2c2;
}
.about_vision_card{
    
    display: grid;
    grid-template-columns: 40% 60%;
}
.about_vision_card img{
    height: 40vh;
    display: flex;

    align-items: center;
    justify-content: center;
}
.about_vision_text {
    line-height: 30px;
}
.about_vision_text h3{
    font-size: 30px;
    margin-bottom: 10px;
    color: var(--primary);
}
.about_vision_text p{
    
    color: var(--text-gray);
    text-align: justify;
}
@media(max-width:1205px){
    .about_vision_card{
        display: grid;
        grid-template-columns: repeat(2 ,1fr);
    }
}
@media(max-width:1110px){
    .about_vision_card{
        display: grid;
        grid-template-columns: repeat(1 ,1fr);
    }
    .about_vision_card {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
    .about_vision_card img{
        height: 100px;
    }
    .about_vision_text h3{
        text-align: center;
    }
}

@media(max-width:770px){
    .about_showcase{
        height: 50vh;
        display: block;   
        position: relative;
    }
    .showcase_img{
        display: flex;
        align-items: center;
        justify-content: right;
    }
    .showcase_img img{
        height: 500px;
        width: 500px;
        
    
    }
    .aboutshowcase_text{
        position: absolute;
        padding-left:20px ;
        top: 50%;
    }
    .aboutshowcase_text h1{
        font-size: 20px;
    }
  
}
@media(max-width:650px){
    .showcase_img img{
        height: 500px;
        width: 300px;
    }
}
@media(max-width:580px){
    .about_vision{
        width: 90%;
    }
    .about_vision_card{
        font-size: 14px;
        
    }
}
