@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;&display=swap');
:root{
    --primary: #5F7D95;
    --primary-hover: #177add; 
    --neutral: #fff;
    --text-gray: #666666;
    --header-black: #151515;
    --smooth-purple: #0B2239;
    --light-purple:#7666D5;
    --haze:#f3f3f7;
    --dark-gray-bg:#91c0f0;
}
html{
    scroll-behavior: smooth;
    outline: none;
    overflow-x: hidden;
}
body{
    font-family: 'Roboto', sans-serif;
    background-color: var(--haze);
    
}
a{
    text-decoration: none;
    color: unset;
}
.container{
    width:90%;
    margin: auto;
}

.gray_text{
    color: var(--text-gray);
}
.primary_text{
    color: var(--primary);
}
.p-xs{
    font-size: 11px;
    font-family: 'Roboto', sans-serif;
}
.p-sm{
    font-size: 14px;
}
.mtb-sm{
    margin-top: 20px;
    margin-bottom: 20px;
}
.txt-white{
    color: white;

}
.primary_link_btn{
    background-color: var(--primary);
    padding: 15px 25px;
    border-radius: 5px;
   
}
.primary_link_btn:hover{
    background-color: var(--primary-hover);
}
.showcase_paragraph{
    line-height: 40px;
}
button:hover{
    cursor: pointer;
}