.testimonial_header{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 12vh;
    line-height: 25px;
}
.testimonial-section{
    background-color: var(--smooth-purple);
    background-image: url('../../assets/images/blizzard.png');
    width: 70%;
    margin: 8vh auto 15vh auto;
}
.testimonial{
    display: flex;
    align-items: center;
    min-height: 50vh;
    justify-content: center;
}
.testimonial-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 60%;
    padding: 20px;
}
.testimonial-image{
    width: 80px;
    height:80px;
    
}
.testimonial-image img{
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 50px;
    border: 5px solid var(--primary);
}
.testimonial-quote {
    text-align: center;
    margin-top: 10px;
    color: white;
}
.testimonial-quote p{
    text-align: center;
}
.testimonial-quote p span{
    color: var(--primary);
    font-size: 35px;
}
@media(max-width:500px){
    .testimonial-section{
        width: 100%;
    }
    .testimonial-content{
        width: 80%;
    }
}