.partnership-showcase{
    background-color: var(--dark-gray-bg);
    height: 50vh;
    color: var(--smooth-purple);
}
.partnership-content{
    /* display: flex;
    align-items: center;
    justify-content: center;
    
    flex-direction: column; */
    display: flex;
    justify-content: center;
    align-items: center;
    
    height:inherit;
}
.partnership-form-section{
    width: 80%;
    margin: auto;
}
.partnership-form-header{
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
}
.partnership-form{
    width: 100%;
    margin-bottom: 20vh;
}
.partnership-form input{
  
    padding: 15px;
 
}
.input_col1{
    display: grid;
    grid-template-columns: 49.7% 49.6%;
    grid-gap: 0.5rem;
}
.input_col2{
    width: 100%;
    margin-top: 20px;
}
.input_col2 input{
    width: 100%;
}
.input_col3{
    width: 100%;
}
.input_col3 input{
    width: 100%;
    margin-top: 20px;
}
.partnership-form textarea{
    width: 100%;
    height: 20vh;
    margin-top: 20px;
    padding: 20px;
    resize: none;
}
.partnership-form button{
    background-color: var(--primary);
    color: white;
    border: none;
    padding: 15px 30px;
    font-size: 17px;
    margin-top: 20px;
}
@media(max-width:750px){
    .partnership-showcase{
        padding: 20px;
        height: 35vh;
        text-align: center;
    }
}
@media(max-width:660px){
    .input_col1{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        
    }
}